@import url(https://fonts.googleapis.com/css?family=Bungee+Shade|Fascinate+Inline&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html{
  scroll-behavior: smooth;
}

.App {
  text-align: center;
  background-color: #D0D0D0;
 
}

.App-logo {
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #09d3ac;
}

#headImage{
  background-image: url(/ReactRestaurant/static/media/bread-bun.994932a6.jpeg);
  background-size: 100vw;
  color: white;
  height: 50vh;
  margin-bottom: 0px;
  padding-top: 0px;
}

.tabs{
  /* background-color: blue; */
  display: block;
  margin-left: auto;
  margin-right: auto;
}

td{
  text-align: right;

}

#itemDesc{
  text-align: center;
}

#itemTitle{
  text-align: left;
  font-weight: bolder;
}

.tab-content{
  max-width: 800px;
  margin: auto;
}

#headImage{
  font-family: 'Fascinate Inline', cursive;
}

@media (max-width: 690px){
#headImage{
  background-image: url(/ReactRestaurant/static/media/bread-bun-chopping-board-tall.19543b1d.jpg);
  
}
}


